.chat-header{
    display: flex;
    justify-content: space-between;
    height: 45px;
    padding: 10px;
    background-color: #F0F3F4;
}

.profile{
    display: flex;
    height: 100%;
    gap: 25px;
}

.profile-pic{
    height: 100%;
    border-radius: 50%;
}

.name{
    margin: 0;
    padding: 0;
    font-size: large;
    font-weight: 600;
}

.online-status{
    font-size: smaller;
}

.text-container{
    display: flex;
    flex-direction: column;
}

.icon-wrapper-chat-header{
    height: 100%;
    display: flex;
    align-items: center;
    gap: 30px;
    margin-right: 20px;
}

.icons-chat-header{
    font-size: 25px;
    cursor: pointer;
}